import React, { useState, useEffect, useRef, useContext } from "react";
import { v4 as uuidv4 } from "uuid";
import { TextField, Button, Box, Checkbox } from "@mui/material";
import styled from "styled-components";
import SvgIcon from "../../../../components/SvgIcon";
import { motion } from "framer-motion";

const BoxCard = styled("div")(({ theme }) => ({
  background: theme.palette.background.light,
  boxShadow: theme.shadow.main,
  padding: 15,
  borderRadius: theme.border.radius,
  width: "100%",
  maxWidth: 550,
}));

const TodoItem = styled("li")(({ theme, completed }) => ({
  padding: "5px 9px 5px 0",
  border: `1px solid ${theme.palette.background.lighter}`,
  display: "flex",
  alignItems: "center",

  "& > p": {
    lineHeight: 1.1,
    fontWeight: 300,
    color: completed ? theme.palette.text.main : theme.palette.common.white,
    textDecoration: completed ? "line-through" : "none",
    wordBreak: "break-word",
  },
}));

const Icon = styled(SvgIcon)(({ theme }) => ({
  color: theme.status.color.tbd,
  transition: "color 250ms ease",
  marginLeft: "5px",
  "&:hover": {
    color: "#e36049",
  },
}));

const TodoList = (props) => {
  const [inputText, setInputText] = useState("");
  const [todoList, setTodoList] = useState([]);

  const addTodo = (e) => {
    e.preventDefault();
    if (inputText !== "") {
      setTodoList([
        ...todoList,
        { text: inputText, completed: false, id: uuidv4() },
      ]);
      setInputText("");
    }
  };

  const toggleTask = (id) => {
    let newTodoList = todoList.map((el) =>
      el.id === id ? { ...el, completed: !el.completed } : el
    );
    setTodoList(newTodoList);
  };

  const deleteTodo = (id) => {
    let newList = [...todoList];
    setTodoList(newList.filter((e) => e.id !== id));
  };

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, transition: { delay: 0.1 } }}
      className="flex grow flex-col mb-16 justify-center items-center"
    >
      <BoxCard>
        <form onSubmit={addTodo}>
          <Box>
            <TextField
              label="Type your task"
              className="w-full"
              variant="filled"
              onChange={(event) => setInputText(event.target.value)}
              value={inputText}
              fullWidth
            />
          </Box>
          <div className="mt-2">
            <Button
              className="w-full"
              variant="contained"
              color="primary"
              fullWidth
              type="submit"
              disableRipple
            >
              Add Todo
            </Button>
          </div>
        </form>
        <ul>
          {todoList.map((el) => (
            <TodoItem completed={el.completed} className="mt-2" key={el.id}>
              <Checkbox
                checked={el.completed}
                onChange={() => toggleTask(el.id)}
                inputProps={{ "aria-label": "controlled" }}
              />
              <p>{el.text}</p>

              <button className="ml-auto" onClick={() => deleteTodo(el.id)}>
                <Icon className="w-5">heroicons-outline:trash</Icon>
              </button>
            </TodoItem>
          ))}
        </ul>
      </BoxCard>
    </motion.div>
  );
};
export default TodoList;
