import { createTheme } from "@mui/material/styles";

export const theme = {
  border: {
    radius: 6,
  },
  shadow: {
    main: "rgba(0, 0, 0, 0.15) 0px 25px 50px -12px;",
  },
  font: {
    family: `"Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Helvetica, Arial, sans-serif`,
  },
  palette: {
    background: {
      main: "#181a20",
      light: "#1e212c",
      lighter: "#252834",
      hover: {
        main: "#282b38",
        light: "#3a3d48",
      },
    },
    text: {
      main: "#646879",
    },
    border: {
      main: "#262a34",
    },
    common: {
      white: "white",
      black: "black",
    },
  },
  status: {
    background: {
      tbd: "#262a34",
      progress: "#6a93bd",
      done: "#7fbd6a",
    },
    color: {
      tbd: "#646879",
      progress: "white",
      done: "white",
    },
  },
};

export const mui = createTheme({
  typography: {
    fontFamily: theme.font.family,
    fontWeightLight: 300,
    fontWeightRegular: 400,
    fontWeightMedium: 500,
  },
  components: {
    MuiButtonBase: {
      defaultProps: {
        disableRipple: true,
      },
    },
    MuiCheckbox: {
      defaultProps: {
        disableRipple: true,
      },
      styleOverrides: {
        root: {
          color: theme.palette.background.hover.light,
        },
      },
    },
    MuiButton: {
      defaultProps: {
        variant: "text",
        color: "inherit",
      },
      styleOverrides: {
        root: {
          borderRadius: theme.border.radius,
        },
        sizeMedium: {
          height: 40,
          minHeight: 40,
          maxHeight: 40,
        },
        contained: {
          boxShadow: "none",
          "&:hover, &:focus": {
            boxShadow: "none",
          },
        },
      },
    },
    MuiFilledInput: {
      styleOverrides: {
        root: {
          backgroundColor: theme.palette.background.main,
          color: theme.palette.common.white,
          "&:hover": {
            backgroundColor: theme.palette.background.main,
          },
          "&.Mui-focused": {
            backgroundColor: theme.palette.background.main,
          },
          "&:before, &:after": {
            display: "none",
          },
        },
      },
    },
    MuiInputLabel: {
      defaultProps: {
        color: "primary",
      },
      styleOverrides: {
        filled: {
          color: theme.palette.text.main,
          "&.Mui-focused": {
            color: theme.palette.text.main,
          },
        },
      },
    },
  },
  palette: {
    primary: {
      main: "#8500ff",
    },
  },
});
