import React, { useState, useEffect, useRef, useContext } from "react";
import { useDispatch } from "react-redux";
import { data } from "./components/data";
import SvgIcon from "../../../../components/SvgIcon";
import styled from "styled-components";
import { TextField, Button, Box } from "@mui/material";
import { openModal } from "../../../../store/modal";
import { motion } from "framer-motion";

const Image = styled("img")(({ theme }) => ({
  width: "100%",
  maxWidth: "400px",
  borderRadius: theme.border.radius,
  margin: "0 auto",
}));

const BoxCard = styled("div")(({ theme }) => ({
  background: theme.palette.background.light,
  boxShadow: theme.shadow.main,
  padding: 15,
  borderRadius: theme.border.radius,
}));

const Title = styled("h4")(({ theme }) => ({
  color: theme.palette.common.white,
  fontWeight: 600,
}));

const Subtitle = styled("h6")(({ theme }) => ({
  color: theme.palette.text.main,
  fontWeight: 300,
}));

const Score = styled("h1")(({ theme }) => ({
  color: theme.palette.common.white,
  fontSize: 120,
  fontWeight: 800,
  lineHeight: 1,
}));

const Icon = styled(SvgIcon)(({ theme, answerResult }) => ({
  color: answerResult === "check" ? "#7fbd6a" : "#e36049",
  margin: "0 auto",
  width: 120,
}));

const Quiz = (props) => {
  const [score, setScore] = useState(0);
  const [allData, setAllData] = useState(data);
  const [currentArtist, setCurrentArtist] = useState(null);
  const [inputText, setInputText] = useState("");
  const [answerResult, setAnswerResult] = useState(null);

  useEffect(() => {
    randomArtist(allData);
  }, [allData]);

  const randomArtist = (arr) => {
    var n = Math.floor(Math.random() * arr.length);
    setCurrentArtist(arr[n]);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (inputText === currentArtist.name) {
      setAnswerResult("check");
      setScore(score + 1);
      setAllData(allData.filter(({ name }) => name !== currentArtist.name));
    } else {
      setAnswerResult("x");
    }
    setInputText("");
  };

  useEffect(() => {
    if (answerResult) {
      let timer1 = setTimeout(() => setAnswerResult(null), 250);
      return () => {
        clearTimeout(timer1);
      };
    }
  }, [answerResult]);

  return (
    <>
      {currentArtist && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1, transition: { delay: 0.1 } }}
          className="flex grow flex-col mb-16 justify-center items-center"
        >
          <Title>Do you know who this is?</Title>
          <Subtitle>Show your knowledge by guessing this singer/band</Subtitle>
          <div className="flex items-end my-4">
            <BoxCard>
              <Image src={currentArtist.images[1].url} />
            </BoxCard>
            <div className="ml-4">
              <div className="text-center mb-8">
                <p>Your score</p>
                <Score>
                  {answerResult ? (
                    <Icon
                      answerResult={answerResult}
                      className="w-5 align-middle p-0.5"
                    >
                      {`heroicons-outline:${answerResult}`}
                    </Icon>
                  ) : (
                    score
                  )}
                </Score>
              </div>
              <BoxCard>
                <div>
                  <form onSubmit={handleSubmit}>
                    <Box
                      sx={{
                        width: 350,
                        maxWidth: "100%",
                      }}
                    >
                      <TextField
                        label="Type your guess"
                        className="w-full"
                        variant="filled"
                        onChange={(event) => setInputText(event.target.value)}
                        value={inputText}
                      />
                    </Box>
                    <div className="mt-2">
                      <Button
                        className="w-full"
                        variant="contained"
                        color="primary"
                        fullWidth
                        type="submit"
                        disableRipple
                      >
                        submit
                      </Button>
                    </div>
                  </form>
                </div>
              </BoxCard>
            </div>
          </div>
        </motion.div>
      )}
    </>
  );
};

export default Quiz;
