import React from "react";
import styled from "styled-components";

const Result = styled("div")(({ theme }) => ({
  display: "flex",
  fontSize: 35,
  fontWeight: 700,
  justifyContent: "end",
  alignItems: "center",
  lineHeight: 1,
  padding: "15px 8px",
  marginBottom: "15px",
  borderRadius: theme.border.radius,
  backgroundColor: "#252834",
  color: theme.palette.common.white,
  maxWidth: "300px",
  minHeight: 65,
  width: "100%",
  overflow: "auto",
}));

export const Display = ({ old, operator, current }) => {
  return (
    <Result>
      <div>{old !== 0 && old}</div>
      <div>{old !== 0 && operator}</div>
      <div>{current}</div>
    </Result>
  );
};
